<template>
  <div class="content_search">
    <div class="header">
      <van-search v-model="value" placeholder="输入影城名字" :show-action="true" @cancel="$router.back()"/>
    </div>
    <div class="value" v-show="!value">
      <p>离你最近</p>
      <ul>
        <li v-for="value in start" @click="clickShunt(value)">{{ value.name }}</li>
      </ul>
    </div>
    <div class="sss" v-show="value">
      <ul>
        <li v-for="value in search" @click="clickShunt(value)">
          <p class="p1">
            <span>{{ value.name }}</span>
            <span>{{ value.address }}</span>
          </p>
          <p class="p2">
            <span v-html="price(value.lowPrice)"></span>
            <span>距离未知</span>
          </p>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {Search, Toast} from 'vant';
  import {getDistance, sort_} from "@/utils/distance.js"
  import axios from "axios";
  import vue from "vue";

  Vue.use(Search);
  export default {
    data() {
      return {
        value: "",
        list: [],
        listxy: [],
        start: [],
        search: []
      }
    },
    mounted() {
      Toast.loading({
        message: 'loading...',
        forbidClick: true,
        duration: 0
      });
      axios({
        url: `https://m.maizuo.com/gateway?cityId=${this.$store.state.cityId}&k=3754946`,
        headers: {
          'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16357749354350673021829121","bc":"370200"}',
          'X-Host': 'mall.film-ticket.cinema.list'
        }
      })
        .then(value => {
          this.list = value.data.data.cinemas;
          let e1 = this.$store.state.longitude
          let n1 = this.$store.state.latitude
          value.data.data.cinemas.forEach(v => {
            let e2 = v.longitude.toFixed(8)
            let n2 = v.latitude.toFixed(8)
            this.listxy.push(getDistance(e1, n1, e2, n2))
            Toast.clear()
          })
          this.sort_()
        })
    },
    methods: {
      sort_() {
        for (let i = 0; i < this.list.length - 1; i++) {
          for (let j = i + 1; j < this.list.length; j++) {
            if (this.listxy[i] > this.listxy[j]) {
              let t = this.list[i];
              this.list[i] = this.list[j];
              this.list[j] = t;
            }
          }
        }
        for (let z = 0; z < 5; z++) {
          this.start.push(this.list[z])
        }
      },


      price(value) {
        if (value) {
          return "￥<span>" + value / 100 + "</span>起"
        } else {
          return "&nbsp;"
        }
      },

      clickShunt(value) {
        console.log(value)
        if (value.lowPrice === 0) {
          this.$router.push(`/main/cinema/${value.cinemaId}`)
        } else {
          this.$router.push(`/main/cinema/${value.cinemaId}/film`)
        }
      }
    },
    watch: {
      value() {
        this.search = []
        this.list.forEach(v => {
          if (v.name.includes(this.value)) {
            this.search.push(v)
          }
        })
      }
    }
  }
</script>

<style scoped lang="scss">

  .content_search {
    height: 100%;
    background: #fff;

    .sss {
      width: 100%;
      position: fixed;
      top: 54px;
      background-color: #fff;
      overflow: auto;
      height: calc(100% - 54px);

      ul {
        width: 100%;
      }

      li {
        padding: 15px;
        width: 100%;
        box-sizing: border-box;
        border-bottom: 0.0625rem solid #ededed;
        display: flex;
      }

      .p1 {
        flex: 1;
        width: 0;
        display: flex;
        justify-content: space-between;
        align-content: flex-end;
        flex-direction: column;
        margin-right: 4.375rem;

        span {
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }

        span:first-of-type {
          font-size: 0.9375rem;
        }

        span:nth-of-type(2) {
          margin-top: 0.3125rem;
          font-size: 0.75rem;
          color: #797d82;
        }

        span:last-of-type {
          margin-top: 0.3125rem;
          font-size: 0.75rem;
          color: #797d82;

          span {
            font-size: 0.9375rem;
          }
        }
      }

      .p2 {
        display: flex;
        text-align: end;
        justify-content: center;
        align-content: flex-start;
        flex-direction: column;
        font-size: 0.625rem;
        width: 4.375rem;

        span:first-of-type {
          color: #ff5f16;

          span {
            color: #ff5f16;
            font-size: 0.9375rem;
          }
        }

        .iconfont {
          color: #797d82;
          text-align: center;
          padding-left: 1.5rem;
          box-sizing: border-box;
        }

        span:last-of-type {
          color: #797d82;
          font-size: 0.625rem;
          margin-top: 0.3125rem;
        }
      }

      .p3 {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 0.3125rem;

        span:first-of-type {
          width: 0;
          max-width: 60%;
          flex: 1;
          font-size: 0.75rem;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          color: #797d82;
        }
      }
    }

    .van-search__action {
      outline: none;
    }

    .value {
      font-size: 13px;
      line-height: 13px;
      color: #bdc0c5;
      margin: 18px 0 18px 15px;
      padding: 0 15px 8px 0;
      position: relative;

      p {
        margin: 18px 0;
      }

      ul {
        display: flex;
        flex-wrap: wrap;

        li {
          height: 30px;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 8px 12px;
          background: hsla(0, 0%, 95.7%, .6);
          color: #191a1b;
          border-radius: 3px;
          box-sizing: border-box;
          margin: 0 10px 10px 0;
        }
      }
    }

    .header {
      position: relative;
    }

    .header::after {
      content: "";
      position: absolute;
      top: auto;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: #ededed;
      transform: scaleY(.5);
    }

    .van-search--show-action {
      padding-right: 15px;
    }

    .van-field__control {
      font-size: 12px !important;
    }

    .van-search__action {
      padding-right: 0;
      padding-left: 15px;
    }

    .van-search__content {
      border-radius: 4px;
      height: 30px;
      background: #f4f4f4;

    }
  }
</style>
